.conversation {
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
}
.conversation:hover{
    background-color: rgb(231, 231, 231);
}

.conversationImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 20px;
}

.conversationName {
    font-weight: 500;
}